import React from 'react';
import pdfjs from 'pdfjs-dist';
import { Document, Page} from 'react-pdf';
//import examplePDF from '/pdfs/Resume.pdf';


function PDFs() {
    pdfjs.GlobalWorkerOptions.workerSrc= '/pdf.worker.js';
    const examplePDF = process.env.PUBLIC_URL + '/pdfs/Resume.pdf';
    return (    
        <div>
        <a href={examplePDF} download>Download PDF</a>

        <Document file={examplePDF}>
        <Page pageNumber={1} />
        </Document>
        </div>
    );
}

export default PDFs;
